import React from 'react'
import styled from 'styled-components'

const Wrapper = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;

    width: 100%;
    padding-bottom: 30px;
    background-color: #1c1b1b;
    
`
const Container = styled.div`
    display: flex;
    align-items: flex-start;
    width: 1200px;
    @media screen and (max-width:760px){
        margin-top: 70px;
        border-top: 1px solid #333;
    }
`
const Ul = styled.ul`
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    @media screen and (max-width:760px){
        display: none;
    }
`
const Li = styled.li`
    display: flex;
    align-items: flex-start;
    list-style: none;
    color: #999;
    font-size: 12px;
    width: calc(100%/4);
    margin: 10px 0;
`
const Sapn = styled.span`
    margin: 0 10px;
`
const Img = styled.img`

`
const items = [
    {
        id : 1,
        img : "assets/footer1.png",
        text : "  "
    },
    {
        id : 2,
        img : "assets/footer2.png",
        text : " "
    },
    {
        id : 3,
        img : "assets/footer3.png",
        text : " "
    },
    {
        id : 4,
        img : "assets/footer4.png",
        text : "2023  (KS-SQI)  A/S  9  1"
    },
    {
        id : 5,
        img : "assets/footer5.png",
        text : "2024 (NCSI)    1  (, , , , RV, RV, )"
    },
    {
        id : 6,
        img : "assets/footer6.png",
        text : "KMAC  2023     30  RV  20  1   /   1"
    },
    {
        id : 7,
        img : "assets/footer7.png",
        text : "2023  (KS-QEI)  10  1 (    , SUV-//,  E, SUV, , AS)"
    }
]
function BottomFooter() {
  return (
    <Wrapper>
        <Container>
            <Ul>
                {items.map((item)=>(
                    <Li key={item.id}><Img src={item.img}></Img><Sapn>{item.text}</Sapn></Li>
                ))}
            </Ul>
        </Container>
    </Wrapper>
  )
}

export default BottomFooter